@import '../../../../public/mixin/index.module'

.course
  display: flex
  padding-bottom: 30px

  &__container
    text-align: center
    width: 100%

  &__progress_container
    align-items: center
    display: flex
    flex-direction: column
    justify-content: center
    width: 100%

  &__progress
    box-shadow: 0 4px 6px rgba(0, 0, 0, .1)
    border-radius: 2rem
    border: 1px solid #e9e9e9
    display: flex
    font-weight: 500
    justify-content: space-between
    margin-top: 10px
    padding: 10px
    position: relative
    width: 1000px

  &__save_progress_container
    padding: 10px
    position: relative
    text-align: right
    width: 1000px

  &__save_progress
    position: absolute
    right: 0

  &__feedback
    align-items: center
    background-repeat: no-repeat
    background-size: 100% 100%
    display: flex
    height: 300px
    justify-content: center
    position: relative
    width: 400px

  &__content_container
    align-items: center
    display: flex
    justify-content: center
    margin-top: 30px

  &__spanishTranslation,
  &__englishWord
    font-size: 40px

  &__content
    display: flex
    justify-content: space-between
    position: relative
    width: 980px

  &__text_language
    color: #f44336
    display: block
    font-size: 20px
    margin-top: 10px

  &__text_grandient
    -webkit-background-clip: text
    background-clip: text
    @include background-gradient()
    color: transparent
    margin: 0
    padding: 0

    &:nth-child(1)
      cursor: pointer

  &__lession_container
    text-align: left

  &__text_lession
    font-size: 18px
    font-weight: 700
    margin-left: 90px
    position: relative
    top: 50px

  &__englishWord
    font-weight: bold

  &__spanishTranslation_container
    padding: 10px 3px

  &__content_text
    display: flex
    flex-direction: column
    font-size: 38px
    font-weight: 900
    justify-content: center
    margin: 0
    width: 27%

    &:first-child
      text-align: left

    &:last-child
      text-align: right

  &__image
    border-radius: 5px
    height: 100%
    width: 100%

  &__bar
    @include background-gradient()
    border-radius: 2rem
    height: 100%
    left: 0
    position: absolute
    top: 0
    transition: all 1s ease
    display: flex
    justify-content: space-between
    align-items: center
    z-index: 1
    overflow: hidden

    span
      white-space: nowrap
      color: white
      padding: 0 10px

  &__arrow
    border-radius: 50%
    cursor: pointer
    height: 53px
    width: 53px

  &__pronunciation
    align-items: center
    display: flex
    justify-content: center
    padding: 20px

  &__message
    color: white
    padding: 10px 20px
    position: absolute
    bottom: 0
    border-top-left-radius: 5px
    border-top-right-radius: 5px

  &__button
    align-items: center
    display: flex
    justify-content: space-between
    width: 400px

  &__speech
    position: relative

    p
      bottom: -50px
      position: absolute
      width: 200px

  &__audio
    display: flex

    img
      margin-top: -1px
      height: 50px !important
      width: 50px !important

  @media only screen and (max-width: 1024px)
    &__content
      padding: 0 40px

    &__container
      margin-top: 15px

    &__message
      margin-left: 0
      padding: 5px

    &__content
      justify-content: space-around
      width: 100%

    &__progress
      width: 90%

    &__lession_container
      text-align: center
      padding: 10px

    &__text_lession
      margin-left: 0
      top: 0

    &__content_text,
    &__text_language,
    &__text_lession,
    &__spanishTranslation,
    &__englishWord
      font-size: 30px

    &__message,
    &__text_language
      font-size: 20px

    &__progress span
      font-size: 18px

  @media only screen and (max-width: 950px)
    &__audio
      img
        height: 45px !important
        margin-top: -3px
        width: 45px !important

    &__button
      width: 258px

    &__content
      padding: 0

    &__content_text
      padding: 20px

    &__arrow,
    &__speech img
      height: 35px
      width: 35px

    &__feedback
      height: 200px
      width: 250px

    &__content_text,
    &__text_language,
    &__text_lession,
    &__spanishTranslation,
    &__englishWord
      font-size: 25px

    &__text_language
      font-size: 15px

    &__message
      font-size: 8px

    &__progress span
      font-size: 15px

  @media screen and (max-width: 950px) and (min-width: 700px)
    &__button
      width: 355px

    &__feedback
      height: 250px
      width: 350px

    &__arrow,
    &__speech img
      height: 45px
      width: 45px

  @media only screen and (max-width: 500px)
    &__audio
      img
        height: 38px !important
        margin-left: -5px
        margin-top: -1px
        width: 38px !important

    &__button
      width: 145px

    &__speech p
      font-size: 12px

    &__textSentence
      font-weight: 400

    &__content
      padding: 0

    &__content_text
      padding: 20px

    &__arrow,
    &__speech img
      height: 28px
      width: 28px

    &__feedback
      height: 130px
      width: 130px

    &__content_text,
    &__text_language,
    &__text_lession,
    &__spanishTranslation,
    &__englishWord
      font-size: 16px

    &__text_language
      font-size: 12px

    &__message
      font-size: 8px

    &__progress span
      font-size: 12px

// Animación para explicación expandible
@keyframes slideDown
  from
    opacity: 0
    transform: translateY(-10px)
    max-height: 0
  to
    opacity: 1
    transform: translateY(0)
    max-height: 500px
